"use client";

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronDownIcon } from 'lucide-react';

export function FAQ() {
  const [openIndex, setOpenIndex] = useState<number | null>(0);
  const faqs = [
    {
      question: 'What makes your Shopify development different?',
      answer:
        'We focus on technical precision and measurable outcomes rather than creative experimentation. Every decision is backed by performance data, conversion optimization principles, and Shopify best practices.'
    },
    {
      question: 'How long does a typical project take?',
      answer:
        'Project timelines vary based on scope and complexity. A custom store build typically takes 6-8 weeks, while migrations and optimizations can be completed in 2-4 weeks. We provide detailed timelines during discovery.'
    },
    {
      question: 'Do you work with Shopify Plus stores?',
      answer:
        'Yes, we specialize in both standard Shopify and Shopify Plus implementations. We have extensive experience with Plus-specific features like Scripts, Flow, and advanced checkout customizations.'
    },
    {
      question: 'What happens after launch?',
      answer:
        'We provide comprehensive documentation, training for your team, and optional ongoing support packages. Our goal is to ensure you can confidently manage your store while having expert backup when needed.'
    },
    {
      question: 'Can you help with an existing store?',
      answer:
        'Absolutely. We offer technical audits, performance optimization, feature additions, and ongoing maintenance for existing Shopify stores. We can work with any theme or custom implementation.'
    }];

  return (
    <section id="faq" className="w-full bg-[#001E21] border-y border-white/5">
      <div className="max-w-container mx-auto px-8 py-20 md:py-32">
        <motion.div
          initial={{
            opacity: 0,
            y: 20
          }}
          whileInView={{
            opacity: 1,
            y: 0
          }}
          viewport={{
            once: true
          }}
          transition={{ duration: 0.6 }}
          className="text-center mb-16">

          <h2 className="text-3xl md:text-[40px] font-bold text-white mb-6">
            Frequently Asked Questions
          </h2>
          <p className="text-lg md:text-[18px] text-slate-400 max-w-2xl mx-auto leading-relaxed">
            Common questions about our services and strategic implementation process.
          </p>
        </motion.div>

        <div className="max-w-3xl mx-auto space-y-4">
          {faqs.map((faq, index) =>
            <motion.div
              key={index}
              initial={{
                opacity: 0,
                y: 10
              }}
              whileInView={{
                opacity: 1,
                y: 0
              }}
              viewport={{
                once: true
              }}
              transition={{
                delay: index * 0.05
              }}
              className="bg-[#013D44] border border-white/5 rounded-[20px] overflow-hidden group">

              <button
                onClick={() => setOpenIndex(openIndex === index ? null : index)}
                className="w-full px-8 py-6 flex items-center justify-between text-left hover:bg-[#024d56]/50 transition-colors"
                aria-expanded={openIndex === index}>

                <span className={`text-[17px] font-bold pr-8 transition-colors duration-300 ${openIndex === index ? 'text-[#CAF389]' : 'text-white'}`}>
                  {faq.question}
                </span>
                <div className={`flex-shrink-0 w-8 h-8 rounded-full border flex items-center justify-center transition-all duration-300 ${openIndex === index ? 'bg-[#CAF389] border-[#CAF389]' : 'border-white/10 group-hover:border-[#CAF389]/50'}`}>
                  <ChevronDownIcon
                    className={`h-4 w-4 transition-transform duration-300 ${openIndex === index ? 'rotate-180 text-[#013D44]' : 'text-slate-400'}`} />
                </div>

              </button>
              <AnimatePresence>
                {openIndex === index &&
                  <motion.div
                    initial={{
                      height: 0,
                      opacity: 0
                    }}
                    animate={{
                      height: 'auto',
                      opacity: 1
                    }}
                    exit={{
                      height: 0,
                      opacity: 0
                    }}
                    transition={{
                      duration: 0.3,
                      ease: "easeInOut"
                    }}>

                    <div className="px-8 pb-8 text-slate-300 leading-relaxed text-[15px] border-t border-white/5 pt-6">
                      {faq.answer}
                    </div>
                  </motion.div>
                }
              </AnimatePresence>
            </motion.div>
          )}
        </div>
      </div>
    </section>);
}