"use client";

import { motion } from 'framer-motion';
import { CheckIcon, XIcon } from 'lucide-react';

export function ProblemSolution() {
  const problems = [
    'Slow page load times hurting conversions',
    'Generic themes that look like competitors',
    'Technical debt from poor implementations',
    'Checkout abandonment from friction',
    'Difficult to manage and update'
  ];

  const solutions = [
    'Performance-first architecture and optimization',
    'Custom designs built for your brand',
    'Clean, maintainable code following best practices',
    'Streamlined checkout flows that convert',
    'Intuitive admin interfaces and documentation'
  ];

  return (
    <section className="w-full bg-[#001E21] border-y border-white/5 relative overflow-hidden">
      {/* Background Decorative Glow */}
      <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[600px] h-[600px] bg-[#CAF389]/5 blur-[120px] rounded-full pointer-events-none" />

      <div className="max-w-container mx-auto px-8 py-20 md:py-32 relative z-10">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-20"
        >
          <h2 className="text-3xl md:text-[40px] font-bold text-white mb-6">
            Common Challenges, Clear Solutions
          </h2>
          <p className="text-lg md:text-[18px] text-slate-400 max-w-2xl mx-auto leading-relaxed">
            We solve the technical problems that hold Shopify stores back from reaching their full potential.
          </p>
        </motion.div>

        <div className="grid md:grid-cols-2 gap-12 lg:gap-24">
          <motion.div
            initial={{ opacity: 0, x: -30 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7 }}
            className="bg-[#013D44]/30 p-10 rounded-[30px] border border-white/5"
          >
            <h3 className="text-2xl font-bold text-white mb-8 flex items-center">
              <span className="w-10 h-10 rounded-xl bg-red-500/10 flex items-center justify-center mr-4">
                <XIcon className="h-6 w-6 text-red-500" />
              </span>
              Typical Problems
            </h3>
            <div className="space-y-6">
              {problems.map((problem, index) => (
                <div key={index} className="flex items-start group">
                  <div className="flex-shrink-0 w-6 h-6 rounded-full border border-red-500/30 flex items-center justify-center mt-1 group-hover:bg-red-500/10 transition-colors">
                    <XIcon className="h-3 w-3 text-red-500" />
                  </div>
                  <p className="ml-4 text-slate-400 group-hover:text-slate-200 transition-colors text-[16px]">
                    {problem}
                  </p>
                </div>
              ))}
            </div>
          </motion.div>

          <motion.div
            initial={{ opacity: 0, x: 30 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.7 }}
            className="bg-[#013D44]/60 p-10 rounded-[30px] border border-[#CAF389]/10 relative shadow-2xl shadow-black/40"
          >
            <h3 className="text-2xl font-bold text-white mb-8 flex items-center">
              <span className="w-10 h-10 rounded-xl bg-[#CAF389]/10 flex items-center justify-center mr-4">
                <CheckIcon className="h-6 w-6 text-[#CAF389]" />
              </span>
              Our Approach
            </h3>
            <div className="space-y-6">
              {solutions.map((solution, index) => (
                <div key={index} className="flex items-start group">
                  <div className="flex-shrink-0 w-6 h-6 rounded-full bg-[#CAF389]/20 flex items-center justify-center mt-1 group-hover:bg-[#CAF389]/30 transition-colors">
                    <CheckIcon className="h-4 w-4 text-[#CAF389]" />
                  </div>
                  <p className="ml-4 text-slate-200 font-medium group-hover:text-white transition-colors text-[16px]">
                    {solution}
                  </p>
                </div>
              ))}
            </div>
          </motion.div>
        </div>
      </div>
    </section>
  );
}