"use client";

import { motion } from 'framer-motion';
import { Card } from './ui/Card';
import { QuoteIcon } from 'lucide-react';

export function Testimonials() {
  const testimonials = [
    {
      quote:
        'The technical precision and attention to performance details exceeded our expectations. Our conversion rate improved significantly within the first month.',
      name: 'Sarah Chen',
      role: 'Director of E-commerce, Fashion Brand',
      avatar:
        'https://images.unsplash.com/photo-1494790108377-be9c29b29330?w=100&h=100&fit=crop'
    },
    {
      quote:
        'Finally, a Shopify agency that understands both the technical and business sides. They delivered a store that scales with our growth.',
      name: 'Michael Rodriguez',
      role: 'CEO, Electronics Retailer',
      avatar:
        'https://images.unsplash.com/photo-1472099645785-5658abf4ff4e?w=100&h=100&fit=crop'
    },
    {
      quote:
        'Clean code, clear communication, and measurable results. The migration was seamless and the performance improvements were immediate.',
      name: 'Emily Watson',
      role: 'VP of Operations, Beauty Brand',
      avatar:
        'https://images.unsplash.com/photo-1438761681033-6461ffad8d80?w=100&h=100&fit=crop'
    }];

  return (
    <section className="w-full bg-[#001E21] border-y border-white/5 overflow-hidden">
      <div className="max-w-container mx-auto px-8 py-20 md:py-32">
        <motion.div
          initial={{
            opacity: 0,
            y: 20
          }}
          whileInView={{
            opacity: 1,
            y: 0
          }}
          viewport={{
            once: true
          }}
          transition={{ duration: 0.6 }}
          className="text-center mb-20">

          <h2 className="text-3xl md:text-[40px] font-bold text-white mb-6">
            Client Success
          </h2>
          <p className="text-lg md:text-[18px] text-slate-400 max-w-2xl mx-auto leading-relaxed">
            What forward-thinking brands say about their journey with us.
          </p>
        </motion.div>

        <div className="grid md:grid-cols-3 gap-8">
          {testimonials.map((testimonial, index) =>
            <motion.div
              key={index}
              initial={{
                opacity: 0,
                y: 20
              }}
              whileInView={{
                opacity: 1,
                y: 0
              }}
              whileHover={{ y: -10 }}
              viewport={{
                once: true
              }}
              transition={{
                duration: 0.5,
                delay: index * 0.1
              }}>

              <Card className="p-10 h-full flex flex-col bg-[#013D44] border-white/5 relative group cursor-default shadow-xl shadow-black/20">
                <QuoteIcon className="absolute top-8 right-8 h-8 w-8 text-[#CAF389]/10 group-hover:text-[#CAF389]/20 transition-colors duration-300" />

                <p className="text-slate-300 leading-relaxed mb-8 flex-1 text-[16px] italic relative z-10">
                  "{testimonial.quote}"
                </p>

                <div className="flex items-center pt-6 border-t border-white/5 mt-auto">
                  <div className="relative">
                    <div className="absolute inset-0 bg-[#CAF389] blur-[8px] opacity-0 group-hover:opacity-20 transition-opacity duration-300 rounded-full" />
                    <img
                      src={testimonial.avatar}
                      alt={testimonial.name}
                      className="w-12 h-12 rounded-full object-cover border-2 border-[#CAF389]/20 relative z-10" />
                  </div>

                  <div className="ml-4">
                    <p className="font-bold text-white text-[16px] group-hover:text-[#CAF389] transition-colors duration-300">
                      {testimonial.name}
                    </p>
                    <p className="text-[13px] text-slate-400 tracking-wide uppercase font-semibold">
                      {testimonial.role}
                    </p>
                  </div>
                </div>
              </Card>
            </motion.div>
          )}
        </div>
      </div>
    </section>);
}