"use client";

import { motion } from 'framer-motion';

export function Process() {
  const steps = [
    {
      number: '01',
      title: 'Discovery',
      description:
        'Technical audit, requirements gathering, and strategic planning to understand your business goals and constraints.'
    },
    {
      number: '02',
      title: 'Design',
      description:
        'Structured design system creation with focus on conversion optimization and brand consistency across all touchpoints.'
    },
    {
      number: '03',
      title: 'Build',
      description:
        'Clean code implementation following Shopify best practices with continuous testing and quality assurance.'
    },
    {
      number: '04',
      title: 'Launch',
      description:
        'Careful deployment with monitoring, performance validation, and comprehensive documentation for your team.'
    }];

  return (
    <section id="process" className="w-full bg-[#001E21] border-y border-white/5 relative overflow-hidden">
      <div className="max-w-container mx-auto px-8 py-20 md:py-32">
        <motion.div
          initial={{
            opacity: 0,
            y: 20
          }}
          whileInView={{
            opacity: 1,
            y: 0
          }}
          viewport={{
            once: true
          }}
          transition={{ duration: 0.6 }}
          className="text-center mb-20">

          <h2 className="text-3xl md:text-[40px] font-bold text-white mb-6">
            Execution Roadmap
          </h2>
          <p className="text-lg md:text-[18px] text-slate-400 max-w-2xl mx-auto leading-relaxed">
            A systematic, data-led approach to delivering high-performance Shopify stores.
          </p>
        </motion.div>

        <div className="grid md:grid-cols-4 gap-12 md:gap-8">
          {steps.map((step, index) =>
            <motion.div
              key={index}
              initial={{
                opacity: 0,
                y: 20
              }}
              whileInView={{
                opacity: 1,
                y: 0
              }}
              viewport={{
                once: true
              }}
              transition={{
                duration: 0.5,
                delay: index * 0.1
              }}
              className="relative group">

              <div className="text-6xl md:text-7xl font-black text-white/[0.03] group-hover:text-[#CAF389]/[0.05] mb-[-1.5rem] md:mb-[-2rem] transition-colors duration-500 font-heading">
                {step.number}
              </div>

              <div className="relative z-10">
                <h3 className="text-2xl font-bold text-white mb-4 group-hover:text-[#CAF389] transition-colors duration-300">
                  {step.title}
                </h3>
                <p className="text-slate-400 group-hover:text-slate-300 leading-relaxed text-[15px] transition-colors duration-300">
                  {step.description}
                </p>
              </div>

              {index < steps.length - 1 &&
                <div className="hidden md:block absolute top-[1.2rem] left-full w-full h-[1px] bg-gradient-to-r from-[#CAF389]/20 to-transparent -translate-x-[20%] pointer-events-none" />
              }

              <div className="mt-8 h-1 w-12 bg-white/10 group-hover:w-full group-hover:bg-[#CAF389] transition-all duration-500 rounded-full" />
            </motion.div>
          )}
        </div>
      </div>
    </section>);
}